body {
    background-color: beige;
}

.app-wrapper {
    display: grid;
    width: 1200px;
    margin: 0 auto;

    grid-template-areas:
    'h h'
    'n c';
    /*grid-template-rows: auto 1fr;*/
    grid-template-rows: 80px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

.app-wrapper-content {
    grid-area: c;
    background-color: cornflowerblue;
}